import React from "react"
import { Link } from "gatsby"
import { ModalRoutingContext } from "gatsby-plugin-modal-routing"

import Zapier from "../components/zapier"

const ModalExamplePage = () => (
  <ModalRoutingContext>
    {({ modal, closeTo }) => (
      <div id="modal">
        {modal ? (
          <Link to={closeTo} className="close">Закрыть</Link>
        ) : (
          <header>
            <h1>Курсы программирования для детей</h1>
          </header>
        )}
        <h1 align="center">Запись на бесплатный пробный урок</h1>
        <p>
          Приглашаем детей от 7 до 17 лет на бесплатный пробный урок от школы
          программирования «Алгоритмика» Нижний Новгород.
        </p>
        <p>
          По окончанию интенсива Ваш ребенок создаст свой первый IT проект:
          программу, игру или мультфильм.
        </p>
        <Zapier />
      </div>
    )}
  </ModalRoutingContext>
)

export default ModalExamplePage
